<template>
    <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
      <a class="navbar-brand col-sm-3 col-md-2 mr-0" href="#" @click.prevent>PaaS UI</a>
        <!-- <input class="form-control form-control-dark w-100" type="text" placeholder="Search" aria-label="Search"> -->  
      <ul id="navbar1" class="navbar-nav px-3">     
        <li class="nav-item text-nowrap px-5">      
          <b-dropdown text="選擇語言" variant="dark">
              <b-dropdown-item @click="toTW()">繁體中文</b-dropdown-item>
              <b-dropdown-item @click="toCH()">簡體中文</b-dropdown-item>
              <b-dropdown-item @click="toEN()">English</b-dropdown-item>
          </b-dropdown>
        </li>  
          <li class="nav-item text-nowrap">
            <a class="nav-link" href="" @click.prevent="logout">登出</a>
          </li>
      </ul>
    </nav>
</template>


<script>
export default {
  name: 'App',
  data(){
      return{
    };
  },
  methods: {
    logout(){
      sessionStorage.removeItem('token');
      this.$router.push('/login');
    },
    toTW(){
      this.$i18n.locale = 'tw'
    },
    toCH(){
      this.$i18n.locale = 'ch'
    },
    toEN(){
      this.$i18n.locale = 'en'
    }
  }
};
</script>
<style>
#navbar1.navbar-nav {
    flex-direction: row;
}

</style>


