<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.RESOURCE_MEM")}}</div>

  <!-- 新建配置開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary"  @click="show = !show">新增資源池</b-btn>
  <br>
  <br>
  <!-- 新建配置結束 -->

  <transition name="slide">
    <aside v-show="show">
      <b-card class="mb-3 border">    
        <form ref="form" @submit.stop.prevent="handleSubmit"  @ok="handleOk">
            <dl class="row">
              <dt class="col text-center">新增資源池</dt>
            </dl>
            <fieldset>
                <div class="form-group">
                    <label class="col col-form-label">資源類型</label>
                    <div class="col">
                      <select class="custom-select custom-select-sm" required>
                        <option v-for="(item) in newResource.type" :value="item.name" >{{item.name}}</option>
                      </select>
                    </div>
                </div>
            </fieldset>
            <fieldset>
                <div class="form-group">
                    <label class="col col-form-label">資源名稱</label>
                    <div class="col">
                      <input v-model="newResource.name" v-validate="'required|numeric'" :class="{'form-control':true, 'is-invalid': errors.has('newResource.name')}" type="text" name="required"/>
                      <span v-show="errors.has('newResource.name')" class="invalid-feedback">{{ errors.first('newResource.name') }}</span>
                    </div>
                </div>
            </fieldset>   
            <fieldset>
                <div class="form-group">
                    <label class="col col-form-label">節點數量</label>
                    <div class="col">
                        <input v-model="newResource.num" v-validate="'required|numeric'" :class="{'form-control':true, 'is-invalid': errors.has('newResource.num')}" type="number" />
                        <span v-show="errors.has('newResource.num')" class="invalid-feedback">{{ errors.first('newResource.num') }}</span>
                    </div>
                </div>
            </fieldset>              
        </form>
        <div class="text-right">
          <b-button size="sm" variant="success" @click="ok()">確定</b-button>
          <b-button size="sm" variant="danger" @click="show = !show">取消</b-button>
        </div>  
      </b-card>    
    </aside>
  </transition>
  
  <!-- 分類表單開始 -->
  <div class="resourceTable">
    <dl class="row resource-heading align-items-center">
      <dt class="col-sm-1">資源池:</dt>
      <dd class="col-sm-9">{{alldata[0].name}}</dd>
      <dd class="col-sm-2 text-right">
        <b-btn variant="outline-light" size="xs">刪除資源池</b-btn>
        <b-btn variant="outline-light" size="xs">擴容</b-btn></dd>
    </dl>
    <v-client-table :data="tableData" :columns="columns" :options="options">   
      <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
        </div>
      </template>
    </v-client-table>
  </div>
  <!-- 分類表單結束 -->

  <!-- 分類表單開始 -->
  <div class="resourceTable">
    <dl class="row resource-heading align-items-center">
      <dt class="col-sm-1">資源池:</dt>
      <dd class="col-sm-9">{{alldata[1].name}}</dd>
      <dd class="col-sm-2 text-right">
        <b-btn variant="outline-light" size="xs">刪除資源池</b-btn>
        <b-btn variant="outline-light" size="xs">擴容</b-btn></dd>
    </dl>
    <v-client-table :data="tableData2" :columns="columns" :options="options">   
      <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
        </div>
      </template>
    </v-client-table>
  </div>
  <!-- 分類表單結束 -->  

  <!-- 分類表單開始 -->
  <div class="resourceTable">
    <dl class="row resource-heading align-items-center">
      <dt class="col-sm-1">資源池:</dt>
      <dd class="col-sm-9">{{alldata[2].name}}</dd>
      <dd class="col-sm-2 text-right">
        <b-btn variant="outline-light" size="xs">刪除資源池</b-btn>
        <b-btn variant="outline-light" size="xs">擴容</b-btn></dd>
    </dl>
    <v-client-table :data="tableData3" :columns="columns" :options="options"> 
      <template slot="action" slot-scope="props">
        <div>
          <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
        </div>
      </template>
    </v-client-table>
  </div>
  <!-- 分類表單結束 -->  


</div>
</template>  

<script>
import Vue from 'vue';
import { mapMutations } from 'vuex'
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
  },
  data(){
    return{
      submittedNames: [],
      selectedIndex: '',
      show: false,
      alldata:[],
      groups:[],
      selectedGroup:'',
      namespace:[],
      selectedNamespace:'',
      tableData: [],
      tableData2: [],
      tableData3: [],
      newResource: {
        type:[
          {
            name:'type1'
          },
          {
            name:'type2'
          }
        ],
        name:'',
        num:''
      },

      columns: ['state', 'spaceName', 'description', 'date', 'id', 'action'],
      options: {
        sortable: ['spaceName', 'description', 'date'],
        filterable: false,
        headings: {
            state: "狀態",
            spaceName: "資源節點",
            description: "CPU",
            date: "MEM",
            id: "Pods",
            action: "操作",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },

      isNew: false,
      isLoading: false,
    };
  },
  created(){
    const vm = this;
    const api = 'http://localhost:3000/tableData/';
    vm.isLoading = true;
    vm.$http.get(api).then((response) => {
      vm.isLoading = false;
      vm.alldata = response.data;
      this.tableData = this.alldata[0].namespace.map((item, index) => {
        item['id'] = index
        return item
      });
      this.tableData2 = this.alldata[1].namespace.map((item, index) => {
        item['id'] = index
        return item
      });
      this.tableData3 = this.alldata[2].namespace.map((item, index) => {
        item['id'] = index
        return item
      });
    })
    .catch(error => {
        alert('商品載入失敗');
    });

  },
  methods:{
    toggleOffsidebar() {
      this.toggleSetting('offsidebarOpen')
    },
    selected() {
      this.selectedIndex = this.groups.indexOf(this.selectedGroup);
      this.namespace = [];
      this.alldata[this.selectedIndex].namespace.forEach((item, index) => {   
        this.namespace.push(item.spaceName);
      });
      this.newConfigure.projectName = this.alldata[this.selectedIndex].name
    },
    loadTable(){
      this.tableData = this.alldata[this.selectedIndex].namespace.map((item, index) => {
          item['id'] = index
          return item
      });
    },
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.nameState = valid ? 'valid' : 'invalid'
      return valid
    },
    handleOk(e) {
      const vm = this;
      e.preventDefault()
      vm.handleSubmit()
    },
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      const vm = this;
      const dataObj = {
        spaceName: '',
        state: "false",
        description: '',
        date: "最新時間",
      };
      dataObj.description = this.text
      this.alldata[this.selectedIndex].namespace.unshift(dataObj);
      
      vm.$http.put(`http://localhost:3000/tableData/${this.selectedIndex + 1}`, 
        this.alldata[this.selectedIndex]
      ).then(resp => {
          console.log(resp.data);
      }).catch(error => {
          console.log(error);
      });

      this.loadTable();
      this.$nextTick(() => {
        this.$refs.modal.hide()
      });
    },
    edit (row) {
      alert(`Editing row id: ${row.id}`);
    },
    remove (row) {
        this.tableData = this.alldata[this.selectedIndex].namespace.filter(item => (row.id !== item.id));
        this.alldata[this.selectedIndex].namespace = this.tableData

        this.$http.put(`http://localhost:3000/tableData/${this.selectedIndex + 1}`, 
          this.alldata[this.selectedIndex]
        ).then(resp => {
            console.log(resp.data);
        }).catch(error => {
            console.log(error);
        });
        
    },
    reload() {
        this.tableData =  tableData;
    },
    clear() {
        this.tableData = [];
    },
    checkCancel(row) { 
      swal({
          title: "確定要刪除?",
          text: "刪除後將無法還原!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "確定!",
          cancelButtonText:"取消",
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: "成功刪除!",
                confirmButtonText: "確定!",
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
}
</script>

<style>
.resource-heading{
  margin: 0;
  padding: 10px 0;
  background-color: #747d9c;
  border-radius: 5px 5px 0 0;
  color: #fff;
  border-left: .5px solid #eee;
  border-right: .5px solid #eee;
}
.resource-heading dt{
  margin-bottom: 0;
  padding-left: 8px;
}
.resource-heading dd{
  margin-bottom: 0;
  padding-left: 8px;
}
.resourceTable thead tr{
  background-color: #747d9c;
}
.resourceTable thead tr th{
  color:#fff;
}
.resourceTable tbody tr{
  background-color: #fafbfc;
}
.resourceTable .VuePagination__count {
  display: none;
}
.slide-enter-active{
	transition: all .3s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.slide-leave-active{
	transition: all .1s ease;
}
.slide-enter, .slide-leave-active{
	transform: translateX(100%);
}
aside{
	width: 300px;
	height: 100vh;
	background: transparent;
  position: fixed;
  z-index: 999999;
	top: 40px;
  right: 0;
} 
aside .card{
	width: 300px;
  height: 100vh;
  border-radius: 0;
}
aside .card fieldset {
  margin-bottom: 10px;
}
</style>


