import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/pages/login'
import Allmenu from '@/components/allmenu'
import Dashboard from '@/components/dashboard'
import Service from '@/components/service'
import Control from '@/components/control'
import Charts from '@/components/Charts'
import NewUser from '@/components/control/NewUser'
import NewProject from '@/components/control/NewProject'
import Resource from '@/components/resource'
import Configure from '@/components/configure'
import Images from '@/components/images'
import Volumes from '@/components/volumes'
import Ingress from '@/components/ingress'
Vue.use(Router)
export default new Router({
  routes: [
	{
		path: '/admin',
		name: 'Dashboard',	
		component: Dashboard,
		meta: { 
			requiresAuth: true 
		},
		children:[
			{
				path: 'control',
				name: 'control',
				component: Control,
				meta: { 
					requiresAuth: true
				},
				children:[
					{
						path: 'charts',
						name: 'charts',
						component: Charts,
						meta: { 
							requiresAuth: true
						},
					},
					{
						path: 'newuser',
						name: 'newuser',
						component: NewUser,
						meta: { 
							requiresAuth: true
						},
					},
					{
						path: 'newproject',
						name: 'newproject',
						component: NewProject,
						meta: { 
							requiresAuth: true
						},
					},
					{
						path: 'resource',
						name: 'resource',
						component: Resource,
						meta: { 
							requiresAuth: true
						},
					}				
				]	
			},
			{
				path: 'service',
				name: 'service',
				component: Service,
				meta: { 
					requiresAuth: false
				},
			},
			{
				path: 'configure',
				name: 'configure',
				component: Configure,
				meta: { 
					requiresAuth: false
				},
			},
			{
				path: 'images',
				name: 'images',
				component: Images,
				meta: { 
					requiresAuth: false
				},
			},
			{
				path: 'volumes',
				name: 'volumes',
				component: Volumes,
				meta: { 
					requiresAuth: false
				},
			},
			{
				path: 'ingress',
				name: 'ingress',
				component: Ingress,
				meta: { 
					requiresAuth: false
				},
			},					
		],
	},
	{  
		path: '/allmenu',
		name: 'allmenu',	
		component: Allmenu,
		meta: { 
			requiresAuth: true 
		}	
	},
    {
		path: '/login',
		name: 'login',
		component: Login
	},
	{
		path: '/',
		component: Login
	},
  ]
})



