<template>
<div>
  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.PRJ_MEM")}}</div>

  <!-- 新建專案開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createdata>新建專案</b-btn><br><br>

  <b-modal class="modal fade" id="createdata" title="新建專案" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="projectName">專案名稱</label>
        <b-form-input type="text" id="projectName" placeholder="請輸入名稱" v-model="projectName" required></b-form-input>
      </div>
      <div class="form-group">
        <label for="textarea-default">專案描述:</label>
        <b-form-textarea id="textarea-default" v-model="projectText" placeholder="請輸入描述" required></b-form-textarea>
      </div>
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">提交</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">取消</b-button>
    </template>
  </b-modal>
  <!-- 新建專案結束 -->
  
  <!-- 分類表單開始 -->
  <template>
      <div>
        <v-client-table class="dark" :data="tableData" :columns="columns" :options="options">
          <template slot="action" slot-scope="props">
            <div>
              <b-btn variant="outline-info mr-2" class="btn-xs" @click.prevent="edit(props.row)" v-b-modal.memberdata>管理</b-btn>
              <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
            </div>
          </template>
        </v-client-table>
      </div>
  </template>
  <!-- 分類表單結束 -->

  <b-modal class="modal fade" id="memberdata" size="lg" title="專案成員管理" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <div class="form-group row justify-content-between">

      <b-tabs class="col-md-4">
        <b-tab title="人員列表"> 
          <div>
            <div class="checkbox c-checkbox" v-for="(item,index) in allmember">
              <label><input type="checkbox" :value="item.name"/><span class="fa fa-check"></span>{{item.name}}</label>
            </div>
          </div>
        </b-tab>
      </b-tabs>         

      <b-tabs class="col-md-6" align="right">
        <b-tab title="專案管理員">
          <div class="col-md-12 mt-2">
            <div class="checkbox c-checkbox" v-for="(item,index) in administrator">
              <label><input type="checkbox" :value="item.name"/><span class="fa fa-check"></span>{{item.name}}</label>
            </div>
          </div>
        </b-tab>
        <b-tab title="專案成員">
          <div class="col-md-12 mt-2">
            <div class="checkbox c-checkbox" v-for="(item,index) in members">
              <label><input type="checkbox" :value="item.name"/><span class="fa fa-check"></span>{{item.name}}</label>
            </div>
          </div>
        </b-tab>
      </b-tabs>

    </div>
  

    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">提交</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">取消</b-button>
    </template>
  </b-modal>


</div>
</template> 

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
Vue.use(ClientTable);

export default {
  components: {
    ClientTable
  },
  data(){
    return{
      projectName:'',
      projectNameState: null,
      projectText:'',
      projectTextState: null,
      submittedNames: [],
      selectedIndex: '',

      alldata:[],
      groups:[],
      selectedGroup:'',
      namespace:[],
      selectedNamespace:'',
      tableData: [],
      allmember:[
        {
          "id":1,
          "name":"Frank"
        },
        {
          "id":2,
          "name":"John Ai"
        }
      ],
      administrator: [
        {
          "id":1,
          "name":"Alston",
          "access": true
        },
        {
          "id":2,
          "name":"administrator1",
          "access": true
        },
        {
          "id":3,
          "name":"administrator2",
          "access": true
        }
      ],
      members:[
        {
          "id":1,
          "name":"Alex Yeh",
          "access": false
        },
        {
          "id":2,
          "name":"member1",
          "access": false
        },
        {
          "id":3,
          "name":"member2",
          "access": false
        }
      ],
      columns: ['projectName', 'state', 'description', 'date', 'action'],
      options: {
        // rowClassCallback: function(row) { return 'bg-primary'},
        sortable: ['projectName', 'state', 'description', 'date'],
        filterable: true,
        texts: {
          filter: '搜尋:',
          filterPlaceholder:"輸入搜尋文字",
          count:"Showing {from} to {to} of {count} records|{count} 筆資料",
          noResults:"共0筆資料",
        },
        headings: {
            projectName: "專案名稱",
            state: "狀態",
            description: "專案描述",
            date: "創建時間",
            action: "操作",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },
      isNew: false,
      isLoading: false,
    };
  },
  created(){
    const api = 'http://localhost:3000/projects/1';
    this.isLoading = true;
    this.$http.get(api).then((response) => {
      this.isLoading = false;
      this.alldata = response.data.project1;
      this.tableData = this.alldata.map((item, index) => {
        item['id'] = index
        return item
      });
    })
    .catch(error => {
        alert('載入失敗');
    });
  },
  methods:{
    loadTable(){
      this.tableData = this.alldata.map((item, index) => {
          item['id'] = index
          return item
      });
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      this.projectNameState = valid ? 'valid' : 'invalid'
      this.projectTextState = valid ? 'valid' : 'invalid'
      return valid
    },
    handleOk(e) {
      e.preventDefault()
      this.handleSubmit()
    },
    resetModal() {
      this.projectName = ''
      this.projectText = ''
      this.projectNameState = null
      this.projectTextState = null
    },
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      const dataObj = {
        projectName: '',
        state: "false",
        description: '',
        date: "最新時間",
      };
      dataObj.projectName = this.projectName
      dataObj.description = this.projectText
      this.alldata.unshift(dataObj);
      this.$http.put('http://localhost:3000/projects/1',
        this.alldata
      ).then(resp => {
          console.log(resp.data);
      }).catch(error => {
          console.log(error);
      });

      this.loadTable();
      this.$nextTick(() => {
        this.$refs.modal.hide()
      });
    },
    edit (row) {

    },
    remove (row) {
        this.tableData = this.alldata.filter(item => (row.id !== item.id));
        this.alldata = this.tableData
        this.$http.put(`http://localhost:3000/projects/`, 
          this.alldata
        ).then(resp => {
            console.log(resp.data);
        }).catch(error => {
            console.log(error);
        }); 
    },
    reload() {
        this.tableData = tableData;
    },
    clear() {
        this.tableData = [];
    },
    checkCancel(row) { 
      swal({
        title: "確定要刪除?",
        text: "刪除後將無法還原!",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#DD6B55",
        confirmButtonText: "確定!",
        cancelButtonText:"取消",
        heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
              title: "成功刪除!",
              confirmButtonText: "確定!",
              type: "success",
              heightAuto: false
            });
          } 
      });
    },
  },
}
</script>
    




