<template>
<div>
  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.ACNT_MEM")}}</div>

  <!-- 新建用戶開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createdata>新建用戶</b-btn><br><br>

  <b-modal class="modal fade" id="createdata" title="新建用戶" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <div class="form-group">
        <label for="userName">用戶名稱</label>
        <b-form-input type="text" id="userName" placeholder="請輸入名稱" v-model="userName" required></b-form-input>
      </div>
      <div class="form-group">
        <label for="password">用戶密碼:</label>
        <b-form-input type="text" id="password" v-model="password" placeholder="請輸入密碼" required></b-form-input>
      </div>
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">提交</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">取消</b-button>
    </template>
  </b-modal>
  <!-- 新建用戶結束 -->


  
  <!-- 分類表單開始 -->
  
  <template>
      <div>
        <!-- <div class="text-right">
          <b-btn @click="clear()" class="mr-2">Clear Table</b-btn>
          <b-btn @click="reload()">Reload Table</b-btn>
        </div> -->
        <v-client-table class="dark" :data="tableData" :columns="columns" :options="options">
          <template slot="action" slot-scope="props">
              <div>
                  <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
              </div>
          </template>
        </v-client-table>
      </div>
  </template>
  <!-- 分類表單結束 -->

</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'
Vue.use(ClientTable);

export default {
  components: {
    ClientTable
  },
  data(){
    return{
      userName:'',
      userNameState: null,
      password:'',
      passwordState: null,
      submittedNames: [],
      selectedIndex: '',

      alldata:[],
      groups:[],
      selectedGroup:'',
      namespace:[],
      selectedNamespace:'',
      tableData: [],
      columns: ['userName', 'date','access','action'],
      options: {
        // rowClassCallback: function(row) { return 'bg-primary'},
        sortable: ['userName', 'date', 'access'],
        filterable: true,
        texts: {
          filter: '搜尋:',
          filterPlaceholder:"輸入搜尋文字",
          count:"Showing {from} to {to} of {count} records|{count} 筆資料",
          noResults:"共0筆資料",
        },
        headings: {
            userName: "用戶名稱",
            date: "創建時間",
            access: "用戶權限",
            action: "操作",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },
      isNew: false,
      isLoading: false,
    };
  },
  created(){
    const vm = this;
    const api = 'http://localhost:3000/NewUser/';
    vm.isLoading = true;
    vm.$http.get(api).then((response) => {
      vm.isLoading = false;
      vm.alldata = response.data;
      this.tableData = this.alldata.map((item, index) => {
        item['id'] = index
        return item
      });
    })
    .catch(error => {
        alert('載入失敗');
    });
  },
  methods:{
    loadTable(){
      this.tableData = this.alldata.map((item, index) => {
          item['id'] = index
          return item
      });
    },
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.userNameState = valid ? 'valid' : 'invalid'
      this.passwordState = valid ? 'valid' : 'invalid'
      return valid
    },
    handleOk(e) {
      const vm = this;
      e.preventDefault()
      vm.handleSubmit()
    },
    resetModal() {
      const vm = this;
      vm.userName = ''
      vm.password = ''
      vm.userNameState = null
      vm.passwordState = null
    },
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      const vm = this;
      const dataObj = {
        userName: "",
        password: "",
        date: "最新時間",
        access: "集群成員"
      };
      dataObj.userName = this.userName
      dataObj.password = this.password
      this.alldata.unshift(dataObj);
      console.log(this.alldata);
      // vm.$http.put('http://localhost:3000/NewUser/', 
      //   this.alldata.unshift(dataObj)
      // ).then(resp => {
      //     console.log(resp.data);
      // }).catch(error => {
      //     console.log(error);
      // });

      this.loadTable();
      this.$nextTick(() => {
        this.$refs.modal.hide()
      });
    },
    edit (row) {
      alert(`Editing row id: ${row.id}`);
    },
    remove (row) {
        this.tableData = this.alldata.filter(item => (row.id !== item.id));
        this.alldata = this.tableData
        this.$http.put(`http://localhost:3000/NewUser/`, 
          this.alldata
        ).then(resp => {
            console.log(resp.data);
        }).catch(error => {
            console.log(error);
        });
        
    },
    reload() {
        this.tableData =  tableData;
    },
    clear() {
        this.tableData = [];
    },
    checkCancel(row) { 
      swal({
          title: "確定要刪除?",
          text: "刪除後將無法還原!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "確定!",
          cancelButtonText:"取消",
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: "成功刪除!",
                confirmButtonText: "確定!",
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
}
</script>
    




