<template>
  <div>
    <allmenu/> 
    <navbar/>
    <sidebar/>   
    <section class="section-container">
      <ContentWrapper>
        <router-view/>
      </ContentWrapper>  
    </section>
        
  </div>
</template>

<script>
import allmenu from './allmenu'
