<template>
<div>

  <div class="card card-default">
      <div class="card-body">
          <form-wizard>
            <template slot="step" slot-scope="props">
              <wizard-step
                  :tab="props.tab"
                  @click.native="props.navigateToTab(props.index)"
                  @keyup.enter.native="props.navigateToTab(props.index)"
                  :transition="props.transition"
                  :index="props.index">
                <span slot="title" :class="{'text-danger':props.tab.validationError}" v-html="props.tab.title"></span>
              </wizard-step>
            </template>
            <!-- Headers -->
          <!-- <form @submit.prevent="validateBeforeSubmit('demo')" data-vv-scope="demo"> -->
            <tab-content title="<div>基本訊息</div>">
              <b-card class="mb-3 border">
              <!-- START card-->
                <dl class="row">
                  <dt class="col-sm-2">專案名稱</dt>
                  <dd class="col-sm-9">{{demo.projectName}}</dd>
                  <dt class="col-sm-2">命名空間</dt>
                  <dd class="col-sm-9">{{demo.spaceName}}</dd>
                </dl>
                <fieldset>
                    <div class="form-group row">
                        <label class="col-md-2 col-form-label">服務名稱</label>
                        <div class="col-md-6">
                            <input v-model="demo.name" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.name')}" type="text" name="required"/>
                            <span v-show="errors.has('demo.name')" class="invalid-feedback">{{ errors.first('demo.name') }}</span>
                        </div>
                    </div>
                </fieldset>
                <fieldset>
                    <div class="form-group row">
                        <label class="col-md-2 col-form-label">容器鏡像</label>
                        <div class="col-md-6">
                            <input v-model="demo.containerMirror" v-validate="'required|numeric'" :class="{'form-control':true, 'is-invalid': errors.has('demo.containerMirror')}" type="text" name="required"/>
                            <span v-show="errors.has('demo.containerMirror')" class="invalid-feedback">{{ errors.first('demo.containerMirror') }}</span>
                        </div>
                    </div>
                </fieldset>
                <fieldset>
                    <div class="form-group row">
                        <label class="col-md-2 col-form-label">容器端口</label>
                        <div class="col-md-3">
                            <input v-model="demo.containerMirror" v-validate="'required|numeric'" :class="{'form-control':true, 'is-invalid': errors.has('demo.containerMirror')}" type="text" name="required"/>
                            <span v-show="errors.has('demo.containerMirror')" class="invalid-feedback">{{ errors.first('demo.containerMirror') }}</span>
                            <button class="btn btn-labeled btn-success mb-2 mr-1 mt-3" type="button">
                              <span class="btn-label"><i class="fa fa-plus"></i></span>增加端口
                            </button> 
                        </div>
                    </div>                  
                </fieldset>
              <!-- END card-->
              </b-card> 
            </tab-content>

            <tab-content title="<div>環境變數</div>">
              <b-card class="mb-3 border">
                <fieldset>
                    <div class="form-group row align-items-end">
                      <div class="col-md-5">
                          <label class="col-form-label">變量</label>
                          <input v-model="demo.environVar" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.environVar')}" type="text" name="required"/>
                          <span v-show="errors.has('demo.environVar')" class="invalid-feedback">{{ errors.first('demo.environVar') }}</span>
                      </div>
                      <div class="col-md-1">
                        <h4> = </h4>
                      </div>
                      <div class="col-md-5">
                          <label class="col-form-label">值</label>
                          <input v-model="demo.environVal" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.environVal')}" type="text" name="required"/>
                          <span v-show="errors.has('demo.environVal')" class="invalid-feedback">{{ errors.first('demo.environVal') }}</span>
                      </div>
                      <div class="col-md-1">
                        <v-btn>刪除</v-btn>
                      </div>
                    </div>
                    <button class="btn btn-labeled btn-success mb-2 mr-1 mt-3" type="button">
                      <span class="btn-label"><i class="fa fa-plus"></i></span>新增變量
                    </button>
                </fieldset>
                <fieldset>
                    <div class="form-group row align-items-end">
                      <div class="col-md-5">
                        <label class="col-form-label">從配置項匯入:</label>
                        <select class="custom-select custom-select-sm" required>
                          <option v-for="(item) in demo.configInput" :value="item.name" >{{item.name}}</option>
                        </select>
                      </div>
                    </div>
                    <button class="btn btn-labeled btn-success mb-2 mr-1 mt-3" type="button">
                      <span class="btn-label"><i class="fa fa-plus"></i></span>從配置項匯入
                    </button>
                </fieldset> 
              </b-card>                   
              </tab-content>

              <tab-content title="<div>標籤</div>">
              <b-card class="mb-3 border">
                <div class="form-group row align-items-end">
                  <div class="col-md-5">
                      <label class="col-form-label">變量</label>
                      <input v-model="demo.tagVar" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.tagVar')}" type="text" name="required"/>
                      <span v-show="errors.has('demo.tagVar')" class="invalid-feedback">{{ errors.first('demo.tagVar') }}</span>
                  </div>
                  <div class="col-md-1">
                    <h4> = </h4>
                  </div>
                  <div class="col-md-5">
                      <label class="col-form-label">值</label>
                      <input v-model="demo.tagVal" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.tagVal')}" type="text" name="required"/>
                      <span v-show="errors.has('demo.tagVal')" class="invalid-feedback">{{ errors.first('demo.tagVal') }}</span>
                  </div>
                  <div class="col-md-1">
                    <v-btn>刪除</v-btn>
                  </div>
                </div>
                <button class="btn btn-labeled btn-success mb-2 mr-1" type="button">
                  <span class="btn-label"><i class="fa fa-plus"></i></span>新增變量
                </button>
              </b-card>      
              </tab-content>

              <tab-content title="<div>數據卷</div>">
                <button class="btn btn-labeled btn-success mb-2 mr-1" type="button">
                  <span class="btn-label"><i class="fa fa-plus"></i></span>雲硬碟
                </button>
                <button class="btn btn-labeled btn-success mb-2 mr-1" type="button">
                  <span class="btn-label"><i class="fa fa-plus"></i></span>配置項
                </button>
                <button class="btn btn-labeled btn-success mb-2 mr-1" type="button">
                  <span class="btn-label"><i class="fa fa-plus"></i></span>NFS
                </button>

                <fieldset class="cloudDrive" v-for="(item) in demo.cloudDrive">
                  <div class="form-group row">
                    <label class="col-md-2 col-form-label">名稱</label>
                    <div class="col-md-3">
                        <input v-model="item.name" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.name')}" type="text" name="required"/>
                        <span v-show="errors.has('item.name')" class="invalid-feedback">{{ errors.first('item.name') }}</span>
                    </div>
                  </div>
                  <div class="form-group row">
                      <label class="col-md-2 col-form-label">雲硬碟</label>
                      <div class="col-md-4">
                        <select class="custom-select custom-select-sm" required>
                          <option v-for="(child) in item.valumes" :value="child.name" >{{child.name}}</option>
                        </select>
                      </div>
                      <label class="col-md-2 col-form-label">掛載路徑</label>
                      <div class="col-md-4"> 
                        <input v-model="item.containerPath" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.containerPath')}" type="text" name="required"/>
                        <span v-show="errors.has('item.containerPath')" class="invalid-feedback">{{ errors.first('item.containerPath') }}</span>
                      </div>
                  </div>
                </fieldset>

                <fieldset class="NFS" v-for="(item) in demo.NFS">
                  <div class="form-group row">
                    <label class="col-md-2 col-form-label">名稱</label>
                    <div class="col-md-4">
                        <input v-model="item.name" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.name')}" type="text" name="required"/>
                        <span v-show="errors.has('item.name')" class="invalid-feedback">{{ errors.first('item.name') }}</span>
                    </div>
                    <label class="col-md-2 col-form-label">主機</label>
                    <div class="col-md-4">
                        <input v-model="item.ip" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.name')}" type="text" name="required"/>
                        <span v-show="errors.has('item.ip')" class="invalid-feedback">{{ errors.first('item.ip') }}</span>
                    </div>
                  </div>
                  <div class="form-group row">
                      <label class="col-md-2 col-form-label">路徑</label>
                      <div class="col-md-4">
                        <input v-model="item.path" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.path')}" type="text" name="required"/>
                        <span v-show="errors.has('item.path')" class="invalid-feedback">{{ errors.first('item.path') }}</span>
                      </div>
                      <label class="col-md-2 col-form-label">掛載路徑</label>
                      <div class="col-md-4"> 
                        <input v-model="item.containerPath" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.containerPath')}" type="text" name="required"/>
                        <span v-show="errors.has('item.containerPath')" class="invalid-feedback">{{ errors.first('item.containerPath') }}</span>
                      </div>
                  </div>
                </fieldset>

                <fieldset class="config" v-for="(item) in demo.config">
                  <div class="form-group row">
                    <label class="col-md-2 col-form-label">名稱</label>
                    <div class="col-md-4">
                        <input v-model="item.name" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.name')}" type="text" name="required"/>
                        <span v-show="errors.has('item.name')" class="invalid-feedback">{{ errors.first('item.name') }}</span>
                    </div>
                  </div>
                  <div class="form-group row">
                      <label class="col-md-2 col-form-label">配置檔</label>
                      <div class="col-md-4">
                        <select class="custom-select custom-select-sm" required>
                          <option v-for="(child) in item.files" :value="child.name" >{{child.name}}</option>
                        </select>
                      </div>
                      <label class="col-md-2 col-form-label">掛載路徑</label>
                      <div class="col-md-4"> 
                        <input v-model="item.containerPath" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.containerPath')}" type="text" name="required"/>
                        <span v-show="errors.has('item.containerPath')" class="invalid-feedback">{{ errors.first('item.containerPath') }}</span>
                      </div>
                  </div>
                </fieldset>                      
              </tab-content>


              <tab-content title="<div>服務部屬策略</div>">
                <b-card class="deployform mb-3 border">
                  <div class="row justify-content-center">
                      <div class="col-md-5 my-3">
                        <fieldset>
                          <label class="switch">
                              <input type="radio" checked="checked" name="radioSwitch"/>
                              <span></span>一般
                          </label>
                          <div class="form-inline">
                            <label class="col-form-label">固定實例數: </label>
                            <input v-model="demo.defaultDeploy.default" v-validate="'required'" :class="{'form-control':true}" type="number" name="required"/>
                          </div>
                        </fieldset>  
                      </div>
                      
                      <div class="col-md-5 my-3">
                        <fieldset>
                          <label class="switch">
                            <input type="radio" name="radioSwitch"/>
                            <span></span>自動伸縮
                          </label>
                        </fieldset>
                        <fieldset>
                          <div class="form-inline">
                            <label class="col-form-label">觸發門檻(CPU使用率): </label>
                            <input v-model="demo.defaultDeploy.default" v-validate="'required'" :class="{'form-control':true}" type="number" name="required"/>
                            <span>%</span>
                          </div>
                        </fieldset>
                        <fieldset>
                          <div class="form-inline">
                            <label class="col-form-label">最小實例數: </label>
                            <input v-model="demo.defaultDeploy.default" v-validate="'required'" :class="{'form-control':true}" type="number" name="required"/>
                          </div>
                        </fieldset>
                        <fieldset>
                          <div class="form-inline">
                            <label class="col-form-label">最大實例數: </label>
                            <input v-model="demo.defaultDeploy.default" v-validate="'required'" :class="{'form-control':true}" type="number" name="required"/>
                          </div>
                        </fieldset>
                      </div>
                        
                  </div>
                </b-card>
              </tab-content> 


              <tab-content title="<div>實例資源限制</div>">
              <b-card class="mb-3 border">
              <fieldset>  
                <div class="form-group row align-items-end">
                  <label class="col-md-2 col-form-label">CPU 請求</label>
                  <div class="col-md-3">
                      <input v-model="demo.cpuRequest" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.cpuRequest')}" type="number" name="required"/>
                      <span v-show="errors.has('demo.cpuRequest')" class="invalid-feedback">{{ errors.first('demo.cpuRequest') }}</span>
                  </div>
                  <span> milli CPUs </span>
                </div>
              </fieldset>
              <fieldset>  
                <div class="form-group row align-items-end">
                  <label class="col-md-2 col-form-label">CPU 限制</label>
                  <div class="col-md-3">
                      <input v-model="demo.cpuLimit" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.cpuLimit')}" type="number" name="required"/>
                      <span v-show="errors.has('demo.cpuLimit')" class="invalid-feedback">{{ errors.first('demo.cpuLimit') }}</span>
                  </div>
                  <span> milli CPUs </span>
                </div>
              </fieldset>
              <fieldset>
                <div class="form-group row align-items-end">
                  <label class="col-md-2 col-form-label">內存 請求</label>
                  <div class="col-md-3">
                      <input v-model="demo.saveRequest" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.saveRequest')}" type="number" name="required"/>
                      <span v-show="errors.has('demo.saveRequest')" class="invalid-feedback">{{ errors.first('demo.saveRequest') }}</span>
                  </div>
                  <span> MiB </span>
                </div>
              </fieldset>
              <fieldset> 
                <div class="form-group row align-items-end">
                  <label class="col-md-2 col-form-label">內存 限制</label>
                  <div class="col-md-3">
                      <input v-model="demo.saveLimit" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('demo.saveLimit')}" type="number" name="required"/>
                      <span v-show="errors.has('demo.saveLimit')" class="invalid-feedback">{{ errors.first('demo.saveLimit') }}</span>
                  </div>
                  <span> MiB </span>
                </div>
              </fieldset>
        <dl class="row ml-2">
          <dt>請求:</dt>
          <dd><small>服務實例運行時所需的最小資源需求，只有當系統可分配資源大於請求時服務實例才能被啟用。</small></dd>
        </dl> 
        <dl class="row ml-2">
          <dt>限制:</dt>  
          <dd><small>服務實例運行時可使用的最大資源限制。</small></dd>
        </dl> 
              </b-card>                 
              </tab-content>


              <tab-content title="<div>端口安全組</div>">
                <b-card class="mb-3 border">
                  <dl class="row">
                    <dt class="col-12">
                      <label class="switch">
                        <input type="checkbox" :checked="demo.isOnly" />
                        <span class="ml-2"></span>
                        <small class="ml-2">啟用時表示僅開放下列設定之端口，一般來說應至少輸入容器端口。</small>
                      </label>
                    </dt>
                  </dl>
                  <fieldset>
                    <label class="col-form-label">開放端口</label>
                    <div class="col-5" v-for="(item) in demo.hostPorts">
                      <input v-model="item.port" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.port')}" type="text" name="required"/>
                      <span v-show="errors.has('item.port')" class="invalid-feedback">{{ errors.first('item.port') }}</span>
                    </div>
                  </fieldset>
                  <button class="btn btn-labeled btn-success" type="button">
                    <span class="btn-label"><i class="fa fa-plus"></i></span>增加開放端口
                  </button>
                </b-card>
              </tab-content>

              <tab-content title="<div>負載設置</div>">
                <b-card class="mb-3 border">
                  <fieldset>
                    <div class="form-group row align-items-end">
                      <div class="col-md-5">
                        <label class="col-form-label">負載均衡策略:</label>
                        <select class="custom-select custom-select-sm" required>
                          <option v-for="(item) in demo.loadBalance" :value="item.name" >{{item.name}}</option>
                        </select>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset>
                    <div class="form-group row align-items-end">
                      <div class="col-md-5">
                        <label class="col-form-label">熔斷機制:</label>
                        <div class="col-md-10">
                            <label class="switch">
                                <input type="checkbox" checked="checked" />
                                <span></span>
                            </label>
                        </div>
                      </div>
                    </div>
                  </fieldset>  
                </b-card>
              </tab-content>   
            <!-- </form> -->


              <!-- Directions -->
              <b-btn variant="secondary" slot="prev">上一步</b-btn>
              <b-btn variant="secondary" slot="next">下一步</b-btn>
              <b-btn variant="info" slot="finish">完成</b-btn>
          </form-wizard>
      </div>
  </div>  

</div>
</template>  

<script>
import Vue from 'vue';
import { FormWizard, TabContent, WizardStep } from 'vue-form-wizard'
import VeeValidate from 'vee-validate';
Vue.use(VeeValidate, {
    fieldsBagName: 'formFields'  // fix issue with b-table
})


export default {
  components: {
    FormWizard,
    TabContent,
    WizardStep
  },
  data(){
    return{
      demo: {
        name: '',
        containerMirror: '',
        environVar: '',
        environVal: '',
        tagVar: '',
        tagVal: '',        
        configInput: [
          {
            name: ''
          }       
        ],
        cloudDrive:[
          {
            name:'',
            valumes:[
              {
                name:'',
              }
            ],
            containerPath:''
          }
        ],
        NFS:[
          {
            name:'',
            valumes:[
              {
                name:'',
              }
            ],
            path:'',
            containerPath:''
          }
        ],
        config:[
          {
            name:'',
            files:[
              {
                name:'',
              }
            ],
            containerPath:'',
          }
        ],
        defaultDeploy:{
          type: Number,
          default: 0
        },
        cpuRequest:'',
        cpuLimit:'',
        saveRequest:'',
        saveLimit:'',
        hostPorts:[
          {
            port:''
          }
        ],
        loadBalance:[
          {
            name:''
          }
        ],
        isOnly: true,
        is: '',
        is_not: ''
      },
    };
  },
}
</script>
    
<style src="vue-form-wizard/dist/vue-form-wizard.min.css"></style>

<style>
.vue-form-wizard .wizard-nav-pills>li>a>span {
    margin-left: 0rem;
    text-align: center;
    width: 100%;
}
.vue-form-wizard .wizard-nav-pills>li>a {
    padding: .5rem !important;
    border-radius: 7px;
}
.vue-form-wizard.md .wizard-icon-circle {
    font-size: 15px;
}
.vue-form-wizard.md .wizard-nav-pills>li.active>a .wizard-icon {
    font-size: 15px;
}
.vue-form-wizard .wizard-nav-pills>li>a>span>div {
    font-weight:bold;
}
.cloudDrive, .NFS, .config, .cloudDrive:last-child, .NFS:last-child, .config:last-child{
  padding: 20px;
  border: 1px dashed #eee;
  margin-bottom: 20px;
}
.form-group h4{
  text-align: center;
}
.deployform .form-inline{
  margin: 10px 0;
}
.deployform input{
  margin: 0px 5px;
}
.deployform fieldset{
  padding: 5px 0;
  margin: 0;
}
fieldset:last-child{
  margin-bottom: 0;
}
</style>



