<template>
<div>

  <!-- 讀取畫面 -->
  <loading :active.sync="isLoading"></loading>

  <div class="content-heading">{{$t("sidebar.SVC_MEM")}}</div>
  <!-- select選項開始 -->
  <b-form class="was-validated">
    <div class="form-group row">
      <label class="col-md-2 col-form-label text-md-right">專案名稱:</label>
      <div class="col-md-4">
        <select class="custom-select custom-select-md mb-3" v-model="selectedGroup" @change="selected(),loadTable()" required>
          <option value="" disabled selected>請選擇專案名稱</option>
          <option v-for="(item) in groups" :value="item" >{{item}}</option>
        </select>
      </div>
      <label class="col-md-2 col-form-label text-md-right">命名空間:</label>
      <div class="col-md-4">
        <select class="custom-select custom-select-md mb-3" v-model="selectedNamespace" @change="loadTable" required>
          <option value="" disabled selected>請選擇命名空間</option>
          <option v-for="item in namespace" :value="item">{{item}}</option>
        </select>
      </div>
    </div>
  </b-form>
  <!-- select選項結束 -->


  <!-- 新建配置開始 -->
  <b-btn type="button" class="btn btn-primary mr-1 mb-1" variant="primary" v-b-modal.createConfigure>新建配置</b-btn>
<br>
<br>
  <b-modal class="modal fade configure" id="createConfigure" size="xl" title="新建配置" ref="modal"  @show="resetModal" @hidden="resetModal" @ok="handleOk">
    <form ref="form" @submit.stop.prevent="handleSubmit">
      <b-card class="mb-3 border">
        <fieldset>
          <dl class="row">
            <dt class="col-sm-3">專案名稱</dt>
            <dd class="col-sm-9">{{newConfigure.projectName}}</dd>
            <dt class="col-sm-3">命名空間</dt>
            <dd class="col-sm-9">{{newConfigure.spaceName}}</dd>
          </dl>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">配置項名稱</label>
                <div class="col-4">
                    <input v-model="newConfigure.name" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('newConfigure.name')}" type="text" name="required"/>
                    <span v-show="errors.has('newConfigure.name')" class="invalid-feedback">{{ errors.first('newConfigure.name') }}</span>
                </div>
            </div>
        </fieldset>
        <fieldset>
            <div class="form-group row">
                <label class="col-2 col-form-label">配置項描述</label>
                <div class="col-10">
                    <input v-model="newConfigure.description" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('newConfigure.description')}" type="text" name="required"/>
                    <span v-show="errors.has('newConfigure.description')" class="invalid-feedback">{{ errors.first('newConfigure.description') }}</span>
                </div>
            </div>
        </fieldset>        
        <fieldset>
            <div class="form-group row align-items-end"  v-for="(item) in newConfigure.variables">
              <div class="col-5">
                  <label class="col-form-label">變量</label>
                  <input v-model="item.var" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.var')}" type="text" name="required"/>
                  <span v-show="errors.has('item.var')" class="invalid-feedback">{{ errors.first('item.var') }}</span>
              </div>
              <div class="col-1">
                <h4 class="text-center"> = </h4>
              </div>
              <div class="col-5">
                  <label class="col-form-label">值</label>
                  <input v-model="item.val" v-validate="'required'" :class="{'form-control':true, 'is-invalid': errors.has('item.val')}" type="text" name="required"/>
                  <span v-show="errors.has('item.val')" class="invalid-feedback">{{ errors.first('item.val') }}</span>
              </div>
              <div class="col-1">
                <v-btn>刪除</v-btn>
              </div>
            </div>
          <br>  
          <button class="btn btn-labeled btn-success mb-2 mr-1" type="button">
            <span class="btn-label"><i class="fa fa-plus"></i></span>新增變量
          </button>  
        </fieldset>
      </b-card>    
    </form>
    <template v-slot:modal-footer="{ok, cancel}">
      <b-button size="sm" variant="success" @click="ok()">提交</b-button>
      <b-button size="sm" variant="danger" @click="cancel()">取消</b-button>
    </template>
  </b-modal>
  <!-- 新建配置結束 -->
  
  <!-- 分類表單開始 -->
  <v-client-table :data="tableData" :columns="columns" :options="options">
    <template slot="action" slot-scope="props">
      <div>
        <b-btn variant="outline-info" class="btn-xs" @click.prevent="edit(props.row)">詳細資訊</b-btn>
        <b-btn variant="outline-danger" class="btn-xs" @click.prevent="checkCancel(props.row)">刪除</b-btn>
      </div>
    </template>
  </v-client-table>
  <!-- 分類表單結束 -->


</div>
</template>  

<script>
import Vue from 'vue';
import { ClientTable,  Event } from 'vue-tables-2';
import swal from 'sweetalert2'

Vue.use(ClientTable);

export default {
  components: {
    ClientTable,
  },
  data(){
    return{
      name:'',
      nameState: null,
      text:'',
      textState: null,
      submittedNames: [],
      selectedIndex: '',

      alldata:[],
      groups:[],
      selectedGroup:'',
      namespace:[],
      selectedNamespace:'',
      tableData: [],

      newConfigure:{
        projectName:'',
        spacetName:'',
        name:'',
        description:'',
        variables:[
          {
            var:'',
            val:''
          }
        ]
      },

      columns: ['spaceName', 'description', 'date', 'action'],
      options: {
        sortable: ['spaceName', 'description', 'date'],
        filterable: true,
        texts: {
          filter: '搜尋:',
          filterPlaceholder:"輸入搜尋文字",
          count:"Showing {from} to {to} of {count} records|{count} 筆資料",
          noResults:"共0筆資料",
        },
        headings: {
            spaceName: "配置名稱",
            description: "描述",
            date: "創建時間",
            action: "操作",
        },
        pagination: { chunk: 5 },
        sortIcon: {
            is: 'fa-sort',
            base: 'fas',
            up: 'fa-sort-up',
            down: 'fa-sort-down'
        },
      },

      isNew: false,
      isLoading: false,
    };
  },
  created(){
    const vm = this;
    const api = 'http://localhost:3000/tableData/';
    vm.isLoading = true;
    vm.$http.get(api).then((response) => {
      vm.isLoading = false;
      vm.alldata = response.data;
      vm.alldata.forEach((item, index) => {      
        vm.groups.push(item.name);
      });
    })
    .catch(error => {
        alert('商品載入失敗');
    });
  },
  methods:{
    selected() {
      this.selectedIndex = this.groups.indexOf(this.selectedGroup);
      this.namespace = [];
      this.alldata[this.selectedIndex].namespace.forEach((item, index) => {   
        this.namespace.push(item.spaceName);
      });
      this.newConfigure.projectName = this.alldata[this.selectedIndex].name
    },
    loadTable(){
      this.tableData = this.alldata[this.selectedIndex].namespace.map((item, index) => {
          item['id'] = index
          return item
      });
    },
    checkFormValidity() {
      const vm = this;
      const valid = vm.$refs.form.checkValidity();
      this.nameState = valid ? 'valid' : 'invalid'
      return valid
    },
    handleOk(e) {
      const vm = this;
      e.preventDefault()
      vm.handleSubmit()
    },
    resetModal() {
      const vm = this;
      vm.name = ''
      vm.text = ''
      vm.nameState = null
      vm.textState = null
    },
    handleSubmit() {
      if (!this.checkFormValidity()) {
        return
      }
      const vm = this;
      const dataObj = {
        spaceName: '',
        state: "false",
        description: '',
        date: "最新時間",
      };
      dataObj.spaceName = this.name
      dataObj.description = this.text
      this.alldata[this.selectedIndex].namespace.unshift(dataObj);
      
      vm.$http.put(`http://localhost:3000/tableData/${this.selectedIndex + 1}`, 
        this.alldata[this.selectedIndex]
      ).then(resp => {
          console.log(resp.data);
      }).catch(error => {
          console.log(error);
      });

      this.loadTable();
      this.$nextTick(() => {
        this.$refs.modal.hide()
      });
    },
    edit (row) {
      alert(`Editing row id: ${row.id}`);
    },
    remove (row) {
        this.tableData = this.alldata[this.selectedIndex].namespace.filter(item => (row.id !== item.id));
        this.alldata[this.selectedIndex].namespace = this.tableData

        this.$http.put(`http://localhost:3000/tableData/${this.selectedIndex + 1}`, 
          this.alldata[this.selectedIndex]
        ).then(resp => {
            console.log(resp.data);
        }).catch(error => {
            console.log(error);
        });
        
    },
    reload() {
        this.tableData =  tableData;
    },
    clear() {
        this.tableData = [];
    },
    checkCancel(row) { 
      swal({
          title: "確定要刪除?",
          text: "刪除後將無法還原!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "確定!",
          cancelButtonText:"取消",
          heightAuto: false
      }).then((result) => {
          if(result.value){
          this.remove(row);
            swal({
                title: "成功刪除!",
                confirmButtonText: "確定!",
                type: "success",
                heightAuto: false
            });
          } 
      });
    },
  },
}
</script>

<style>
#createConfigure .modal-xl {
  max-width: 900px;
}
#configure  fieldset:last-child{
  margin-bottom: 0;
}
</style>


