<template>
  <div>  
    
<div id="chartmem">
    <svg preserveAspectRatio="xMidYMid" width="100%" height="100%" viewBox="0 -10 400 270" data-value="0">
        <g class="arcInner">
            <path d="M311.967,243.783 L298.006,235.733 C308.140,218.068 313.965,197.620 313.965,175.802 C313.965,109.106 259.827,55.038 193.045,55.038 C126.262,55.038 72.126,109.106 72.126,175.802 C72.126,197.228 77.739,217.335 87.536,234.780 L73.575,242.830 C62.407,223.015 56.004,200.161 56.004,175.802 C56.004,100.214 117.360,38.937 193.045,38.937 C268.731,38.937 330.085,100.214 330.085,175.802 C330.085,200.554 323.473,223.749 311.967,243.783 Z" class="cls-1"/>
        </g>
        <g class="pointerRect" transform="translate(-5 -25)">
            <g transform="rotate(150, 199, 200)">
                <path d="M230.996,200.282 C230.996,200.282 207.282,215.837 198.377,215.633 C189.470,215.429 182.413,208.061 182.612,199.176 C182.814,190.290 190.198,183.252 199.104,183.456 C208.011,183.660 230.996,200.282 230.996,200.282 Z"
                      class="pointer" transform="rotate(150, 199, 200)"/>
            </g>
        </g>
        <g class="outerText">
            <text transform="translate(35 258) scale(1.34 1.34)">
                <tspan>0</tspan>
            </text>
            <text transform="translate(-1 107) scale(1.34 1.34)">
                <tspan>25%</tspan>
            </text>
            <text transform="translate(168 17) scale(1.34 1.34)">
                <tspan>50%</tspan>
            </text>
            <text transform="translate(332 107) scale(1.34 1.34)">
                <tspan>75%</tspan>
            </text>
            <text transform="translate(332 258) scale(1.34 1.34)">
                <tspan>100%</tspan>
            </text>
        </g>
        <g>
            <text>
                <tspan x="265" y="210" fill="#fff" class="cls-font1 targetName"></tspan>
                <tspan x="265" y="225" fill="#31B836" class="cls-font1 targetValue"></tspan>
            </text>
            <path class="target"/>
        </g>
        <g>
            <text x="195" y="130">
                <tspan class="cls-7">
                    <tspan class="textDynamic">0</tspan>
                    <tspan class="cls-8">%</tspan>
                </tspan>
            </text>
        </g>
    </svg>
    <div class="header text-center" style="color:#fff; font-size:1.2rem;font-weight:bold;background:#5d9cec;max-width: 60%;margin: 20px 23% 0px 17%;">
        MEM
    </div>
</div>

  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name:'chartmem',
  data(){
    return{
      cpuName:'#chartmem',
      show:true,
      value:70,
      currentValue:0,
      targetValue:100,
    }
  },
  methods:{
    updateData(e){
      redraw( {path:this.cpuName, value:this.value , currentValue:this.currentValue} );
      this.currentValue = this.value;
    }
  },
  mounted(){
    this.updateData();
  }
};

    /*
    * @param paramObj.path
    * @retrun
    */
    function redraw(paramObj) {
        var path = paramObj.path || "";
        var value = paramObj.value || 0;
        var targetValue = paramObj.targetValue;

        var svg = d3.select(paramObj.path).select("svg");
        var currentValue = paramObj.currentValue||0;

        svg.selectAll('g.arc').remove();

        if (value >= 100) value = 100;
        else if (value <= 0) value = 0;

        var currentPoint = (valueToDegreesPoint(currentValue || 0));

        var arcInner = d3.arc()
            .outerRadius(112)
            .innerRadius(146);
        var pie = d3.pie();
        var number = [100];

        function addTarget() {
            var target = svg.select(".target");
            var tValue = svg.select(".targetValue");
            var tName = svg.select(".targetName");
            var targetPath, tx, ty;
            if (targetValue >= 100) targetValue = 100;
            else if (targetValue <= 0) targetValue = 0;
            var trad = valueToRadians(targetValue);
            ty = Math.round((175 - Math.sin(trad) * 110) * 100) / 100;
            tx = Math.round((194 - Math.cos(trad) * 110) * 100) / 100;
            if (tx > 195) {
                targetPath = ['M', tx, ty, 'L', tx - 5, ty + 4, 'L', tx - 5, ty - 4, 'z'];
                target.attr('d', targetPath.join(' '));
                tValue.attr('y', ty + 4)
                    .attr('x', tx - 35)
                    .text(targetValue + "%");
                tName.attr('y', ty - 10)
                    .attr('x', tx - 35)
                    .text("目标");
            } else {
                targetPath = ['M', tx, ty, 'L', tx + 5, ty + 4, 'L', tx + 5, ty - 4, 'z'];
                target.attr('d', targetPath.join(' '));
                tValue.attr('y', ty + 4)
                    .attr('x', tx + 8)
                    .text(targetValue + "%");
                tName.attr('y', ty - 10)
                    .attr('x', tx + 8)
                    .text("目标");
            }
        }

        function outerArc() {
            var arcs = svg.selectAll('g.arc')
                .data(pie(number))
                .enter()
                .append('g')
                .attr('class', "arc");
                arcs.append("svg:path")
                .attr("class", "arcDynamic")
                .attr("transform", function () {
                        return "translate(" + 194 + ", " + 175 + ") rotate(270)"
                    }
                )
                .transition() 
                .ease(d3.easeBounce)
                .duration(2000) 
                .attrTween('d', tweenPieInner)
        }

        function tweenPieInner(b) {
            b.startAngle = valueToRadians(0);
            b.endAngle = valueToRadians(value);
            var i = d3.interpolate(
                { startAngle: valueToRadians(0), 
                  endAngle: valueToRadians(currentValue || 0)
                },
                b
            );
            currentValue = value;
            return function (t) {
                var it = i(t);
                return arcInner(it);
            };
        }

        function pointerRotate() {
            var pointer = svg.select(".pointer");
            pointer.transition()
                .duration(2000)
                .ease(d3.easeBounce)
                .attrTween("transform", function () {
                    var targetRotation = (valueToDegreesPoint(value));
                    var currentRotation = currentPoint || targetRotation;
                    if (currentPoint === 0) {
                        currentRotation = 0
                    }
                    return function (step) {
                        var rotation = currentRotation + (targetRotation - currentRotation) * step;
                        var textValue = value * step; 
                        textValue = Math.round(textValue * 100) / 100; 
                        svg.select(".textDynamic") 
                            .text(textValue); 
                        return " rotate(" + rotation + ", " + 200 + ", " + 200 + ")";  
                    }
                })
        }

        

        if (targetValue) {
            addTarget();
        }

        pointerRotate();
        outerArc();
        changeColor();

        
        function changeColor() {
        }
    }

function valueToRadians(value) {
  return valueToDegrees(value) * Math.PI / 180;
}

function valueToDegrees(value) {
  return value / 100 * 240.5 - 30;
}

function valueToDegreesPoint(value) {
  return value / 100 * 242 - 2;
}

</script>

<style>
#chartmem {
  width: 100%;
  margin: 0 auto;
  color:white;
}
#chartmem .outerText {
  font-size: 17.8248px;
  font-weight: bold;
}

#chartmem .outerText,
#chartmem .cls-7 {
  fill: #000;
  font-family: "Arial";
}

#chartmem .cls-7 {
  font-size: 35.6495px;
  text-anchor: middle; 
  dominant-baseline: auto; 
}

#chartmem .cls-8 {
  font-size: 22.281px;
}

#chartmem .cls-font1 {
  font-size: 10px;
}

#chartmem .cls-1 {
  fill: #ccc;
}

#chartmem .arcDynamic,
#chartmem .pointer {
  fill: #5d9cec;
}

#chartmem .target {
  fill: #31B836;
}

</style>


